/**
 * 组件样式
 */
.cpm_hide {
  display: none!important;
}
.cpm_left {
  float: left;
}
.cpm_right {
  float: right;
}
.cpm_clear {
  clear: both;
  height: 0;
  width: 100%;
}
.cpm_form_input {
  position: relative;
  background-color: #3c3c3c;
  border-radius: 4px;
}
.cpm_form_input > input {
  width: 100%;
  height: 50px;
  line-height: 50px;
  padding: 0 20px;
  color: #a0a0a0;
  font-size: 14px;
  border: 1px solid #232323;
  border-radius: 4px;
}
.cpm_form_input > input:focus {
  border: 1px solid #e61450;
}
.cpm_form_input span {
  position: absolute;
  top: 50%;
  right: 20px;
  display: none;
  transform: translate(0, -50%);
  color: #5a5a5a;
  font-size: 12px;
}
.cpm_form_input.z-active span {
  display: block;
}
.cpm_form_input.z-error > input {
  border: 1px solid #e61450;
}
.cpm_form_input.z-error span {
  display: block;
}
.cpm_form_input.z-limit span {
  color: #e61e5a;
}
.cpm_form_textarea {
  position: relative;
  padding: 20px;
  border-radius: 4px;
  background-color: #3c3c3c;
  border: 1px solid #232323;
}
.cpm_form_textarea textarea {
  width: 100%;
  height: 100%;
  color: #a0a0a0;
  font-size: 14px;
}
.cpm_form_textarea span {
  position: absolute;
  bottom: 20px;
  right: 20px;
  display: none;
  color: #5a5a5a;
  font-size: 12px;
}
.cpm_form_textarea.z-active span {
  display: block;
}
.cpm_form_textarea.z-error {
  border: 1px solid #e61450;
}
.cpm_form_textarea.z-error span {
  display: block;
}
.cpm_form_textarea.z-limit span {
  color: #e61e5a;
}
.cpm_tip_number {
  min-width: 20px;
  height: 20px;
  padding: 4px 8px;
  border-radius: 10px;
  font-size: 12px;
  color: #dcdcdc;
