.c-dialog__tip {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 2;
  transform: translate(-115%, 0);
}
.c-dialog__tip .tip-arrow {
  position: absolute;
  top: 50%;
  right: -6px;
  margin-top: -6px;
  display: inline-block;
  width: 6px;
  height: 14px;
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../images/icon/tip-arrow.png');
}
.c-dialog__tip .tip-text {
  padding: 0 10px;
  height: 40px;
  line-height: 40px;
  background-color: rgba(0, 0, 0, 0.8);
  color: #e61e5a;
  border-radius: 4px;
  font-size: 14px;
}
.c-dialog__tip.z-right .tip-arrow {
  right: inherit;
  left: -6px;
  transform: rotate(180deg);
}
.c-dialog__tip.z-top {
  top: -125%;
  left: 50%;
  transform: translate(-50%, 0);
}
.c-dialog__tip.z-top .tip-arrow {
  right: inherit;
  top: 100%;
  left: 50%;
  margin-left: -6px;
  margin-top: -4px;
  transform: rotate(90deg);
}



