}
.m-artice-index__content {
  position: relative;
  min-height: 200px;
}
.m-artice-index__content .content-right {
  width: 680px;
  margin-left: 280px;
}
.m-artice-index__content .content-right .title {
  width: 100%;
  height: 60px;
  padding-left: 20px;
  padding-top: 22px;
  font-size: 14px;
  color: #a0a0a0;
  background-color: #2d2d2d;
}
.m-artice-index__content .content-right .title .number {
  font-weight: bolder;
  font-size: 18px;
  color: #e61e5a;
