  padding: 0 60px;
  width: 100%;
  height: 80px;
  padding-top: 20px;
  padding-left: 50px;
  padding-right: 30px;
  overflow: hidden;
  background-color: #414141;
  border-bottom: 1px solid #3c3c3c;
